<template>
  <div>
    <nav class="nav hero is-default has-shadow">
      <div class='container'>
        <div class="nav-left">
          <router-link to="/" class="nav-item">
            <img src='../assets/CITY-LOGO.png' alt="Vue">
          </router-link>
          <input class="title is-3" :value="routerQuery.mapTitle" @input="updateTitle($event.target.value)">
        </div>
      </div>
    </nav>
    <div id="viewDiv" class="balt-theme"></div>
  </div>
</template>

<script>
import * as esriLoader from 'esri-loader'
import { mapGetters } from 'vuex'
import { store } from '@/store/store'
export default {
  data() {
    return {
      mapTitle: 'Create Map'
    }
  },
  computed: mapGetters({
    selectedLayers: 'getSelectedLayers',
    routerQuery: 'getRouterQuery'
  }),
  methods: {
    updateTitle(value) {
      let title = value
      this.$router.push({ name: 'map', query: { mapTitle: title, zoom: this.routerQuery.zoom, ids: this.routerQuery.ids, center: this.routerQuery.center } })
    },
    createMap(loader, router, title) {
      const esriLoader = loader
      esriLoader.dojoRequire(
        [
          'esri/core/watchUtils',
          'esri/views/MapView',
          'esri/Map',
          'esri/layers/VectorTileLayer',
          'esri/layers/Layer',
          'esri/widgets/Expand',
          'esri/widgets/Legend',
          'esri/widgets/LayerList',
          'esri/widgets/Search'
        ],
        (
          watchUtils,
          MapView,
          Map,
          VectorTileLayer,
          Layer,
          Expand,
          Legend,
          LayerList,
          Search
        ) => {
          const urlQuery = this.routerQuery
          const center = urlQuery.center.split(',').map(element => parseFloat(element))
          const ids = urlQuery.ids.split(',')
          const zoom = urlQuery.zoom
          const map = new Map({
            basemap: 'topo-vector'
          })
          const view = new MapView({
            map: map,
            container: 'viewDiv',
            zoom: zoom,
            center: center
          })
          ids.forEach(function (id) {
            Layer.fromPortalItem({
              portalItem: {
                id: id
              }
            }).then(function (layer) {
              map.add(layer)
            })
          })
          view.then(() => {
            const legend = new Legend({
              view: view,
              container: document.createElement('div')
            })
            const layerList = new LayerList({
              view: view,
              container: document.createElement('div')
            })
            const legendExpand = new Expand({
              view: view,
              content: legend.domNode,
              expandIconClass: 'esri-icon-collection',
              expandTooltip: 'Legend'
            })
            const layersExpand = new Expand({
              view: view,
              content: layerList.domNode,
              expandIconClass: 'esri-icon-layer-list',
              expandTooltip: 'Layers'
            })
            view.ui.add(layersExpand, 'top-right')
            view.ui.add(legendExpand, 'top-right')
          })
          const searchWidget = new Search({
            view: view
          })
          view.ui.add(searchWidget, {
            position: 'top-left',
            index: 0
          })
          watchUtils.whenTrue(view, 'stationary', () => {
            let center = [view.center.longitude.toFixed(3), view.center.latitude.toFixed(3)].toString()
            let zoom = view.zoom
            router.push({
              name: 'map',
              query: {
                mapTitle: store.state.route.query.mapTitle,
                zoom: zoom,
                ids: ids.join(','),
                center: center
              }
            })
          })
        }
      )
    }
  },
  mounted() {
    if (!esriLoader.isLoaded()) {
      esriLoader.bootstrap((err) => {
        if (err) {
          console.error(err)
        }
        this.createMap(esriLoader, this.$router, this.routerQuery.mapTitle)
      }, {
          url: 'https://js.arcgis.com/4.4/'
        })
    } else {
      this.createMap(esriLoader, this.$router, this.routerQuery.mapTitle)
    }
  }
}
</script>
<style scoped>
@import url('https://js.arcgis.com/4.4/esri/themes/light/main.css');
.nav.is-default {
  background-color: #f5f5f5;
  margin-bottom: 2rem;
}

.nav-left {
  align-items: center;
}

.nav-item img {
  max-height: 2.75rem;
}

input {

  border: 0;
  background: transparent;
  font-weight: normal;
}

#home {
  position: fixed;
  top: 30px;
  z-index: 10;
}

#viewDiv {
  position: absolute;
  top: 3.5rem;
  bottom: 0;
  left: 0;
  right: 0;
}
</style>



